// CustomException.js
define([], function () {
    class ErrorEqualUrbanLotWithinBlock extends Error {
        constructor(message) {
            super(message || "Se ha encontrado un lote urbano con la misma denominación dentro del mismo ámbito de unidad urbana y manzana urbana.");
            this.name = "ErrorEqualUrbanLotWithinBlock";
        }
    }

    return {
        ErrorEqualUrbanLotWithinBlock: ErrorEqualUrbanLotWithinBlock
    };
});